import React, { Component } from 'react';
import SelectMode from './SelectMode';
import moment from 'moment';

export class CalendarHeader extends Component {
    renderTitle = () =>{
        const { title, mode } = this.props;   

        if (mode === 'month'){
            const monthYear = moment(title).format("YYYY MMMM");  
            return monthYear;
        } else if (mode === 'week') {
            const startOfWeek = moment(title).startOf('week').format('MMM D');
            let endFormat;

            if (moment(moment(title).endOf('week')).isSame(moment(title).startOf('week'), 'month')){
                endFormat = 'D';
            } else {
                endFormat = 'MMM D';
            }
            const endOfWeek = moment(title).endOf('week').format(endFormat);

            return `${startOfWeek} - ${endOfWeek}`;
        }
    }

    renderDaysTitle = () => {
        if (this.props.showDaysTitle) 
        return (
            <div className="days-of-week">
                <span className="day-of-week">S</span>
                <span className="day-of-week">M</span>
                <span className="day-of-week">T</span>
                <span className="day-of-week">W</span>
                <span className="day-of-week">T</span>
                <span className="day-of-week">F</span>
                <span className="day-of-week">S</span>
            </div>
        );
    }

    render() {  
        const { title, mode, showSelectMode} = this.props,
            prevMonth = moment(moment(title).subtract(1, 'months')).format("MMM"),
            nextMonth = moment(moment(title).add(1, 'months')).format("MMM");

    return (
    <div className="calendar-header">
        <div className="calendar-navigation">
            <button className = "nav-btn prev-btn" onClick = {this.props.sub}>{(mode === 'month') ? prevMonth : 'prev'}</button>
            <span onClick = { this.props.SelectMode } className="current-month">{this.renderTitle()}</span>
            <button className = "nav-btn next-btn" onClick = {this.props.add }>{(mode === 'month') ? nextMonth : 'next'}</button>
        </div>
        {this.renderDaysTitle()}
        {(showSelectMode) ? <SelectMode changeMode={this.props.changeMode}/> : ''}
    </div>
    )
  }
}

export default CalendarHeader