import React, { Component } from 'react'

export class SelectMode extends Component {

  render() {
    return (
      <div className="selectmode">
        <button onClick = {this.props.changeMode} className="selectmode"> 
            Month
        </button>
        <button onClick = {this.props.changeMode} className="selectmode">
            Week
        </button>
      </div>
    )
  }
}

export default SelectMode
