import React, { Component } from 'react';
import CalendarHeader from './CalendarHeader';
import Events from './Events';
import moment from 'moment';
import AddFile from './AddFile';

const events = [
  {
      date: '13-02-2019',
      events:[
          {name:'event name', body: 'event body', time: '11:00'},
          {name:'event name2', body: 'event bod2', time: '12:00'}
      ]
  },
  {
      date: '20-02-2019',
      events:[
          {name:'event 3', body: 'event 3', time: '12:00'}
      ]
  },
  {
    date: '14-02-2019',
    events:[
        {name:'event 3', body: 'event 3', time: '12:00'}
    ]
  },
  {
    date: '28-03-2019',
    events:[
        {name:'other month', body: 'other month', time: '00:00'}
    ]
  }
];

export class Calendar extends Component {
  constructor(props){
    super(props);
    this.state = {
      mode: 'month',
      currentContext : moment(),
      selectedDate   : moment(),
      currentDate    : moment(),
      showSelectMode : false,
      showDaysTitle  : true,
      displayAddEvent: false, 
      events: events
    }
  }

  addEvent = () => {

    const e = [...this.state.events];
    e.push({date: moment(this.state.selectedDate).format("DD-MM-YYYY"), events: [{name:'event zeleboba', body: 'kebabster', time: '13:00'}]});
    this.setState({events: e}); 
  }

  
  changeMode = () => {
    if (this.state.mode === 'month'){
      this.setState({mode: 'week'});
    } else if (this.state.mode === 'week') {
      this.setState({mode: 'month'});
    }
    this.setState({showSelectMode: false, showDaysTitle: true});
  }

  SelectMode = () =>{
    if (this.state.showSelectMode === false){
        this.setState({showSelectMode: true, showDaysTitle: false});
    } else {
        this.setState({showSelectMode: false, showDaysTitle: true});
    }
  }

  onDateClick = day => {
    this.setState({
      selectedDate: day
    });
  }

  add = () => {
    if (this.state.mode === 'month'){
      this.setState({
        currentContext: moment(this.state.currentContext).add(1, 'months')
      });
      this.setState({selectedDate: moment(this.state.selectedDate).add(1,'month')});
    } else if (this.state.mode === 'week') {
      this.setState({
        currentContext: moment(this.state.currentContext).add(1, 'week')
      });
      this.setState({selectedDate: moment(this.state.selectedDate).add(1,'week')});
    } 
  }

  sub = () => {
    if (this.state.mode === 'month'){
      this.setState({
        currentContext: moment(this.state.currentContext).subtract(1, 'months')
      });
      this.setState({selectedDate: moment(this.state.selectedDate).subtract(1,'month')});
    } else if (this.state.mode === 'week') {
        this.setState({
          currentContext: moment(this.state.currentContext).subtract(1, 'week')
      });
      this.setState({selectedDate: moment(this.state.selectedDate).subtract(1,'week')});
    } 
  }

  renderWeek = () => {
    const { selectedDate, currentDate } = this.state;
    let startOfWeek;

      startOfWeek = moment(this.state.selectedDate).startOf('week');
   
    const days = [];
    let formattedDate,
        day = startOfWeek;

      for (let i = 0; i < 7; i++) {
      const cloneDay = day;

      formattedDate = moment(day).format("D");
        days.push(
          <div className = {`day 
          ${ (i === 0) ? "weekend" : (i === 6) ? "weekend" : "" }
          ${ moment(day).isSame(currentDate, 'day') ? "current" : 'ordinary' }
          ${ moment(day).isSame(selectedDate, 'day') ? "selected" : 'ordinary'}` }
          key = { day }
          onClick={ () => this.onDateClick(moment(cloneDay, "MM-DD-YYYY"))}
          >
          <p>{formattedDate}</p>
          </div>
        );

      day = day.clone().add(1, 'd');
    }
    return(   
      <div className = "month">
        <div className = "week">
          { days }
        </div>
      </div>      
    ); 
  }

  renderMonth = () => {
    const { selectedDate, currentDate } = this.state;
    const monthStart = moment(selectedDate).startOf('month'),
          monthEnd = moment(monthStart).endOf('month'),
          startDate = moment(monthStart).startOf('week'),
          rows = [];
          
    let days = [],
        day = startDate,
        formattedDate = "";
    
      while (day <= monthEnd) {
        for (let i = 0; i < 7; i++) {
          formattedDate = moment(day).format("D");  
          const cloneDay = day;

          if (!moment(day).isSame(monthStart, 'month')){
            days.push(
              <div className="space" key = {day}></div>
            );
          } else {
            days.push(
              <div className = {`day 
                ${ (i === 0) ? "weekend" : (i === 6) ? "weekend" : "" }
                ${ moment(day).isSame(currentDate, 'day') ? "current" : 'ordinary' }
                ${ moment(day).isSame(selectedDate, 'day') ? "selected" : 'ordinary'}` }
                key = {day}
                onClick={() => this.onDateClick(moment(cloneDay, "DD-MM-YYYY"))}
              >
              <p>{formattedDate}</p>
              </div>
            );
          }
          day = moment(day).add(1, 'days');
        }
        rows.push(
          <div className="week" key = {day}>
            {days}
          </div>
        );
        days = [];
      }
    return <div className="month">{ rows }</div>;
  }

  render() {
    const {mode, selectedDate, showSelectMode, showDaysTitle, events, currentDate} = this.state;
    return (
      <div id="calendar">
        <div className="add-event-panel">
          <button onClick = {this.addEvent}>+</button>
        </div>
        {(this.state.displayAddEvent)?<AddFile addEvent = {this.addEvent} />:''}
        <CalendarHeader title = {selectedDate}
                        mode = {mode}
                        add = {this.add}
                        sub = {this.sub}
                        changeMode = {this.changeMode}
                        showSelectMode = {showSelectMode} 
                        showDaysTitle = {showDaysTitle}
                        SelectMode = {this.SelectMode}
                        />
        { ( mode === 'month' ) ? this.renderMonth() : ( mode === 'week' ) ? this.renderWeek() : '' }
        <Events events={events}
                selectedDate = {selectedDate}
                mode = {mode}
                currentDate = {currentDate}/>
      </div>
    )
  }
}

export default Calendar